import { BASE_URL } from '../core/constants'
import { IOrder } from '../types'
import type { IIngredient } from '../types/Ingredient'
import { IUser } from '../types/User'
import {
    getAccessToken,
    getRefreshToken,
    saveTokens,
    clearTokens,
} from './auth'

interface ISuccessResponse {
    success: boolean
    data: IIngredient[]
    orders: IOrder[]
    user: IUser
    accessToken: string
    refreshToken: string
}

export const checkResponse = (response: Response) => {
    if (response.ok) return response.json()
    return Promise.reject(` ${response.status}`)
}

const checkSuccess = (response: ISuccessResponse) => {
    if (response && response.success) return response
    return Promise.reject(`  success: ${response}`)
}

export const request = async (endpoint: string, options?: RequestInit) => {
    return await fetch(`${BASE_URL}${endpoint}`, options)
        .then(checkResponse)
        .then(checkSuccess)
}

export const requestWithAuth = async (
    endpoint: string,
    options?: RequestInit
) => {
    const accessToken = getAccessToken()

    const headers = {
        ...options?.headers,
        'Content-Type': 'application/json',
        ...(accessToken && { Authorization: `Bearer ${accessToken}` }),
    }

    try {
        const response = await fetch(`${BASE_URL}${endpoint}`, {
            ...options,
            headers,
        })

        if (response.status === 401 || response.status === 403) {
            const refreshToken = getRefreshToken()
            if (!refreshToken) {
                clearTokens()
                throw new Error('No refresh token available')
            }

            const refreshResponse = await fetch(`${BASE_URL}auth/token`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ token: refreshToken }),
            })

            if (!refreshResponse.ok) {
                clearTokens()
                throw new Error('Failed to refresh token')
            }

            const refreshData = await refreshResponse.json()
            saveTokens(refreshData.accessToken, refreshData.refreshToken)

            const newAccessToken = refreshData.accessToken.replace(
                'Bearer ',
                ''
            )
            const retryResponse = await fetch(`${BASE_URL}${endpoint}`, {
                ...options,
                headers: {
                    ...options?.headers,
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${newAccessToken}`,
                },
            })

            return checkResponse(retryResponse).then(checkSuccess)
        }

        return checkResponse(response).then(checkSuccess)
    } catch (error) {
        throw error
    }
}
